.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #000000;
 }

.App-link {
  color: #000000;
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
  margin: 30px auto;
}

select {
  text-align: center;
  padding: 10px;
  margin: 10px auto;
  border-radius: 10px;
}



select:not(select, button) {
  text-align: center;
  color: black;
}

img {
  width: 75%;
  margin-top: 15px;
}

.visual {
  margin: 10px 1px 10px 1px;
}

.menu {
  width: 90%;
}
.menu2 {
  margin: -14px 1px 15px 1px;
  justify-content: center
}
.menu3 {
  width: 31%;
}

.menu4 {
  width: 48%;
}
.button {
  color: black;
}

.resul {
  margin: 10px;
}

 input {
   text-align: center;
 }
